.section.section-masters {
  background-color: $secondaryBgColor;
}

.container-under-title.under-title-masters {
  font-style: normal;
  font-weight: 600;
  font-size: 11px;
  line-height: 1.36;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  margin-bottom: 20px;

  &::before {
    content: '';
    display: inline-block;
    margin-right: 20px;
    vertical-align: middle;
    width: 60px;
    height: 1px;
    background-color: $primaryTextColor;
  }

  @media screen and (min-width: $tablet) and (max-width: $desktop - 1px) {
    margin-left: 119px;
  }
}

.container-title.title-masters {
  margin-bottom: 44px;

  @media screen and (min-width: $tablet) and (max-width: $desktop - 1px) {
    margin-left: 119px;
  }
}
.thumb-photo-master {
  height: 346px;
  margin-bottom: 30px;

  & img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }
}

.list-mastres {
  @media screen and (min-width: $desktop) {
    display: flex;
    margin: -15px;
  }

  & .item-masters {
    max-width: 418px;
    margin-left: auto;
    margin-right: auto;

    @media screen and (min-width: $tablet) {
      max-width: 450px;
    }

    @media screen and (min-width: $desktop) {
      max-width: calc((100% - 6 * 15px) / 3);
      margin: 15px;
    }
  }
}

.item-masters {
  &:not(:last-child) {
    @media screen and (max-width: $desktop - 1px) {
      margin-bottom: 64px;
    }
  }

  & .name-masters {
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.39;
    text-align: center;
    margin-bottom: 8px;
  }

  & .prof-masters {
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 1.36;
    text-align: center;
    color: $orangeAccentColor;
    margin-bottom: 30px;
  }
}

.list-social-masters {
  display: flex;
  justify-content: center;

  li:not(:last-child) {
    margin-right: 10px;
  }

  & .logotype-social-masters {
    display: flex;
    width: 44px;
    height: 44px;
    border-radius: 50%;
    overflow: hidden;
    background-color: $primaryWhiteColor;
    transition: color 250ms $transmition-cubic;
  }

  & .logotype-social-masters:hover,
  & .logotype-social-masters:focus {
    color: $orangeAccentColor;
  }

  & .icon {
    margin: auto;
    fill: currentColor;
  }
}
.active-button:hover,
.active-button:focus {
  animation: button 0.5s ease-in-out infinite both;
}
@-webkit-keyframes active-button {
  0% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}
@keyframes active-button {
  0% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}
